import React from "react"
import { RocketTwoTone } from "@ant-design/icons"
import TweenOne from "rc-tween-one"
import BezierPlugin from "rc-tween-one/lib/plugin/BezierPlugin"

TweenOne.plugins.push(BezierPlugin)

const animation = function () {
  return {
    bezier: {
      type: "soft",
      autoRotate: true,
      vars: Array(20)
        .fill(null)
        .map((v, i) => ({ x: i * 100 + Math.random() * 100, y: Math.random() * 100 })),
    },
    delay: Math.random() * 5000,
    repeat: -1,
    duration: 20000,
  }
}

const ShipsAnimation = (props) => {
  const { ships = 3 } = props
  const animations = React.useMemo(() => Array(ships).fill(null).map(animation), [ships])
  return (
    <div>
      {animations.map((animation, i) => (
        <TweenOne key={i} animation={animation} style={{ margin: 0, position: "absolute", left: -50 }}>
          <RocketTwoTone rotate={90} />
        </TweenOne>
      ))}
    </div>
  )
}

export default ShipsAnimation
